import { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import SwiperBg from '../routes/SwiperBg'
import FastMenu from './FastMenu'
import axios from 'axios'
import { getNewData } from '../store'

function Main() {
    // 통신 시작 , 끝 여부 저장할 state
    let [communication, setCommunication] = useState(false)
   
    const dispatch = useDispatch()

    // 로딩바 보여주기
   // setCommunication(true)
    useEffect(() => {
        axios.get('https://z1z1hh.github.io/shop/data.json')
            .then((response) => { 
                console.log('통신시작')
        
                console.log(communication);
                setCommunication(true);
     
                // 통신 성공
                // 새로 받아온 데이터
                const getData = response.data;
                console.log(getData);
                dispatch(getNewData(getData));

                // 데이터 불러왔으므로 로딩바 숨기기
                setCommunication(false);
            })
            .catch((error) => {
                // 통신 실패
                console.log(error);
                setCommunication(false);
            })  
    }, [])


    return (
        <>  
            <SwiperBg></SwiperBg>
            <FastMenu></FastMenu>
        </>
    )
}

function NoData() {
    return (
        <div>
            <p>더이상 보여줄 상품이 없습니다.</p>
        </div>
    )
}

function LoadingBar() {
    return (
        <div className="loading-wrap">
            <div className="loading-bar">
                <img src="/Spin-loading.gif" />
            </div>
        </div>
    )
    
}

export default Main;